import React, { Component } from "react";
import Button from "react-bootstrap/Button";
import Card from "react-bootstrap/Card";

export default class CardItem extends Component {
  constructor(props) {
    super(props);
    this.state = {
      moreText: true,
      poster_src: this.props.poster_src,
      cardHeight: "45rem"
    };
    this.viewMovie = this.viewMovie.bind(this);
    this.showMore = this.showMore.bind(this);
  }

  viewMovie() {
    const url = "https://www.themoviedb.org/movie/" + this.props.movieID;
    window.location.href = url;
  }

  showMore() {
    var p = document.getElementById(this.props.movieID);
    var li = document.getElementById("link" + this.props.movieID);
    if (this.state.moreText === true) {
      this.state.moreText = false;
      p.innerHTML = this.props.overview;
      this.setState({ cardHeight: "58rem" });
    } else {
      p.innerHTML = this.props.overview.substring(0, 150) + "...more";
      this.setState({ cardHeight: "45rem" });
      this.state.moreText = true;
    }
  }

  shrinkOverview() {
    var p = document.getElementById(this.props.movieID);
    p.innerHTML = this.props.overview.substring(0, 150);
  }

  componentDidMount() {
    var overview = this.props.overview.substring(0, 150);
    var myLi = "link" + this.props.movieID;
    var li = document.getElementById(myLi);
    var p = document.getElementById(this.props.movieID);

    if (this.props.overview.length > 200) {
      li.innerHTML = "...more";
    } else {
      li.innerHTML = "";
    }
  }

  render() {
    return (
      <Card
        className="card"
        bg="light"
        text="black"
        style={{
          width: "16rem",
          height: this.state.cardHeight,
          margin: "10px",
          flex: 1
        }}
      >
        <Card.Img
          variant="top"
          src={
            (this.state.poster_src =
              "https://image.tmdb.org/t/p/w185" + this.props.poster_path)
          }
        />
        <Card.Body>
          <Card.Title>{this.props.title}</Card.Title>
          <Card.Text>
            <p id={this.props.movieID} onClick={this.showMore}>
              {this.props.overview.substring(0, 150)}
              <il id={"link" + this.props.movieID} href="#">
                ...more
              </il>
            </p>
          </Card.Text>
          <Button variant="primary" onClick={this.viewMovie}>
            View
          </Button>
        </Card.Body>
        <Card.Footer>
          <small className="text-muted">
            Release Date: {this.props.release_date}
          </small>
        </Card.Footer>
      </Card>
    );
  }
}
