import React, { Component } from "react";
import CardList from "./CardList.js";
import Spinner from "react-bootstrap/Spinner";
import "./MoviePage.css";
import Space from "../assets/Space.png";
import Navbar from "react-bootstrap/Navbar";
import { NavDropdown, Nav, FormControl, Form } from "react-bootstrap";
import { Button } from "reactstrap";

class MoviePage extends Component {
  constructor(props) {
    super(props);
    this.state = {
      top_rated: true,
      rows: []
    };
    this.performSearch("");
    this.searchChangeHandler = this.searchChangeHandler.bind(this);
  }

  performSearch(searchTerm) {
    let url;

    if (searchTerm === null || searchTerm === "") {
      this.setState({ top_rated: true });
      url =
        "https://api.themoviedb.org/3/movie/top_rated?api_key=6d33d7313e2e4565a9681b4714dd4dc7&language=en-US&page=1";
    } else {
      this.setState({ top_rated: false });
      url =
        "https://api.themoviedb.org/3/search/movie?&api_key=6d33d7313e2e4565a9681b4714dd4dc7&query=" +
        searchTerm;
    }
    fetch(url)
      .then(response => response.json())
      .then(rows => {
        this.setState({ rows: rows.results });
      });
  }

  searchChangeHandler(event) {
    const boundObject = this;
    const searchTerm = event.target.value;
    boundObject.performSearch(searchTerm);
  }
  render() {
    var top;
    this.state.top_rated ? (top = "Top Rated") : (top = "");

    if (!this.state.rows.length) {
      return (
        <div className="text-center">
          <Spinner animation="border" role="status" size="">
            <span className="sr-only ">Loading...</span>
          </Spinner>
        </div>
      );
    }

    return (
      <div className="App">
        {/*  <table className="titleBar">
          <tbody>
            <tr>
              <td>
                <img className="App-logo" src="logo192.png" />
              </td>
              <td width="8"></td>
            </tr>
          </tbody>
        </table> */}

        <Navbar bg="dark" expand="lg" sticky="top">
          <Navbar.Brand style={{ color: "white" }}>Movie Time</Navbar.Brand>
          <Navbar.Toggle aria-controls="basic-navbar-nav" />
          <Navbar.Collapse id="basic-navbar-nav">
            <Form inline>
              <FormControl
                onChange={this.searchChangeHandler}
                type="text"
                placeholder="Search"
                className="mr-sm-1"
              />
            </Form>
          </Navbar.Collapse>
        </Navbar>

        <h2 style={{ color: "white" }} id="top">
          {top}
        </h2>

        <CardList cardList={this.state.rows} />
      </div>
    );
  }
}

export default MoviePage;
