import React, { Component } from "react";
import MoviePage from "./MoviePage.js";
import "./LoginPage.css";
import { Button, Form, FormGroup, Label, Input } from "reactstrap";
import { Link } from "react-router-dom";
import Particles from "react-particles-js";
import Spinner from "react-bootstrap/Spinner";

const particlesOptions = {
  particles: {
    number: {
      value: 30,
      density: {
        enable: true,
        value_area: 200
      }
    }
  }
};

class LoginPage extends Component {
  constructor(props) {
    super(props);
    this.state = {
      signInEmail: "",
      signInPassword: "",
      spinnerOn: false
    };
    this.moveToSite = this.moveToSite.bind(this);
    this.onPasswordChange = this.onPasswordChange.bind(this);
    this.onEmailChange = this.onEmailChange.bind(this);
  }

  onEmailChange(event) {
    this.setState({ signInEmail: event.target.value });
  }

  onPasswordChange(event) {
    this.setState({ signInPassword: event.target.value });
  }

  moveToSite() {
    fetch("https://search-movies-app.herokuapp.com/signin", {
      method: "post",
      headers: { "Content-Type": "application/json" },
      body: JSON.stringify({
        email: this.state.signInEmail,
        password: this.state.signInPassword
      })
    })
      .then(response => response.json())
      .then(user => {
        if (user.id) {
          //this.props.loadUser(user)
          this.props.history.push("/MoviePage");
        } else {
          this.props.history.push("/ErorPage");
        }
      });
  }

  render() {
    if (this.state.spinnerOn) {
      return (
        <Spinner
          animation="border"
          role="status"
          size=""
          className="d-flex justify-content-center"
        >
          <span className="sr-only justify-content-center">Loading...</span>
        </Spinner>
      );
    }

    return (
      <div className="login-page">
        <Particles className="particles" params={particlesOptions} />
        <Form className="Login-form">
          <h1 className="text-center text-white">
            <span className="font-weight-bold ">Movie Time</span>
          </h1>
          <h2 className="text-center text-white">Welcome </h2>
          <FormGroup>
            <Label className="text-white">Email</Label>
            <Input
              type="email"
              placeholder="Email"
              id="inputEmail"
              onChange={this.onEmailChange}
            />
          </FormGroup>
          <FormGroup>
            <Label className="text-white">Password</Label>
            <Input
              type="password"
              placeholder="Password"
              id="inputPassword"
              onChange={this.onPasswordChange}
            />
          </FormGroup>
          <Button
            className="btn-lg btn-dark btn-block"
            onClick={this.moveToSite}
          >
            Log in
          </Button>
          <div className="text-center">
            <Link to="/RegisterPage">
              <a>Sign up</a>
            </Link>
            <span className="p-2">|</span>
            <Link /* to='/Forgot-password' */>
              <a>Forgot Password</a>
            </Link>
          </div>
        </Form>
      </div>
    );
  }
}

export default LoginPage;
